import React from 'react';
import {Routes, Route} from "react-router-dom";
import axios from 'axios';
import Header from './components/Header';
import Drawer from './components/Drawer';
import Home from './routes/home';
import Favorites from './routes/favorites';
import User from './routes/user';
import AppContext from './context';


function App() {
  const [items, setItems] = React.useState([]);
  const [cartItems, setCartItems] = React.useState([]);
  const [favoriteItems, setFavoriteItems] = React.useState([]);
  const [searchValue, setSearchValue] = React.useState('');
  const [cartOpened, setCartOpened] = React.useState(false);
  const [isLoading, setIsLoading] = React.useState(true);
  
  React.useEffect(() => {
    async function fetchData(){
      const [cartRespons, favoriteRespons, itemsResponse] = await Promise.all([
        axios.get('https://62f64d45612c13062b4b33e6.mockapi.io/cart'), 
        axios.get('https://62f64d45612c13062b4b33e6.mockapi.io/favorite'), 
        axios.get('https://62f64d45612c13062b4b33e6.mockapi.io/items')]);
      setIsLoading(false);
      setItems(itemsResponse.data);
      setCartItems(cartRespons.data);
      setFavoriteItems(favoriteRespons.data);
    }

    fetchData();
  }, []);

  const onAddToCart = async (obj) => {
    const findItem = cartItems.find((item) => Number(item.parentId) === Number(obj.id))
    try {
      if (findItem) {
        setCartItems((prev) => prev.filter((item) => Number(item.parentId) !== Number(obj.id)))
        await axios.delete(`https://62f64d45612c13062b4b33e6.mockapi.io/cart/${findItem.id}`);
      } else {
        const { data } = await axios.post('https://62f64d45612c13062b4b33e6.mockapi.io/cart', obj);
        setCartItems((prev) => [...prev, data]);
      }
    } catch (error) {
      alert("Ошибка при добавлении товара в корзину");
      console.log(error);
    }
  };

  const onAddToFavorite = async (obj) => {
    const findFav = favoriteItems.find((item) => Number(item.parentId) === Number(obj.id))
    try {
      if (findFav) {
        setFavoriteItems((prev) => prev.filter((item) => Number(item.parentId) !== Number(obj.id)));
        axios.delete(`https://62f64d45612c13062b4b33e6.mockapi.io/favorite/${findFav.id}`);
      } else {
        const { data } = await axios.post('https://62f64d45612c13062b4b33e6.mockapi.io/favorite', obj);
        setFavoriteItems((prev) => [...prev, data]);
        console.log(data, favoriteItems);
      }
    } catch (error) {
      alert("Ошибка при добавлении товара в избранное");
      console.log(error);
    }
  };

  const onRemoveItem = async (id) => {
    try {
      setCartItems((prev) => prev.filter(item => item.id !== id));
      await axios.delete(`https://62f64d45612c13062b4b33e6.mockapi.io/cart/${id}`);
    } catch (error) {
      alert("Ошибка при удалении товара из корзины");
      console.log(error);
    }
  }

  const isItemAdded = (id) => {
    return cartItems.some((obj) => Number(obj.parentId) === Number(id));
  }
  
  const isItemFavorited = (id) => {
    return favoriteItems.some((obj) => Number(obj.parentId) === Number(id));
  }


  return (
    <AppContext.Provider value={{ items, cartItems, favoriteItems, isItemAdded, onAddToFavorite, setCartOpened, setCartItems, onAddToCart, isItemFavorited }}>
      <div className="wrapper clear">
        {cartOpened && <Drawer items={cartItems} onClickClose={() => setCartOpened(false)} onRemove={onRemoveItem}/>}
        <Header onClickCart={() => setCartOpened(true)} />
        <Routes>
          <Route path="/" element={
            <Home
              items={items}
              searchValue={searchValue} 
              setSearchValue={setSearchValue} 
              onAddToCart={onAddToCart}
              onAddToFavorite={onAddToFavorite}
              isLoading={isLoading}
            />
            } />
          <Route path="favorites" element={<Favorites />} />
          <Route path="user" element={<User />} />
        </Routes>
      </div>
    </AppContext.Provider>
  );
}

export default App;