import React from 'react';
import Card from '../components/Card';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Pagination, Scrollbar, A11y, Autoplay } from 'swiper';
import 'swiper/css/bundle';



export default function Home({
  items, 
  searchValue, 
  setSearchValue, 
  onAddToCart, 
  onAddToFavorite, 
  cartItems, 
  isLoading}) {
  const renderItems = () => {
    const filteredItems = items.filter((item) => item.title.toLowerCase().includes(searchValue.toLowerCase()));

    return (isLoading ? [...Array(8)] : filteredItems).map((item, index) => (
      <Card
        key={index}
        onFavorite={(obj) => onAddToFavorite(obj)}
        onPlus={(obj) => onAddToCart(obj)}
        loading={isLoading}
        {...item}
      />
    ));
  }

    return (
        <div className="content p-40">
          <div className="slider">
            <Swiper
              modules={[Navigation, Pagination, Scrollbar, A11y, Autoplay]}
              spaceBetween={50}
              speed={3000}
              loop={true}
              slidesPerView={1}
              navigation
              autoplay={{ delay: 3000}}
              onSlideChange={() => console.log('slide change')}
              onSwiper={(swiper) => console.log(swiper)}
            >
              <SwiperSlide><img src='/img/slide.png' alt='Slide' /></SwiperSlide>
              <SwiperSlide><img src='/img/slide1.png' alt='Slide2' /></SwiperSlide>
              <SwiperSlide><img src='/img/slide2.png' alt='Slide3' /></SwiperSlide>
            </Swiper>
          </div>
          <div className='d-flex align-center justify-between mb-40'>
            <h1>{searchValue ? `Поиск по запросу: "${searchValue}"` : 'Все кроссовки'}</h1>
            <div className="search-block">
              <img src="/img/search.svg" alt="search" width={15} height={15}/>
              <input onChange={(event) => setSearchValue(event.target.value)} value={searchValue} placeholder='Поиск...' />
            </div>
          </div>
          <div className="d-flex flex-wrap">
            {renderItems()}
          </div>
        </div>
    );
}